import React, { Component } from 'react';
import './App.css';
import YouTube from 'react-youtube';

class App extends Component {

  constructor(props) {
    super(props);
    this.state = {
      currentVideo: {},
      videoList: [],
      after: ''
    }
  }

  setNextVideo(event) {
    this.setState({currentVideo: this.formatCurrentVideo(this.state.videoList, this.state.currentVideo.position)});
  }

  componentDidUpdate() {
    let v = document.getElementsByTagName("video")[0];
    if(v) {
      v.addEventListener('ended', this.setNextVideo.bind(this));
    }
  }

  componentWillUnmount() {
    let v = document.getElementsByTagName("video")[0];
    if(v) {
      v.removeEventListener('ended');
    }
  }

  componentDidMount() {
    // fetch('https://www.reddit.com/r/nba.json').then(resp => resp.json()).then(data => {
    //   let videos = data.data.children.filter(item => {
    //     let url = item.data.url;
    //     return url.match(/streamable/) || url.match(/youtube.com\/watch/) || url.match(/youtu.be/)
    //   })
    //   this.setState({
    //     after: data.data.after,
    //     videoList: videos,
    //     currentVideo: this.formatCurrentVideo(videos, 0)
    //   })
    // })
    this.callRedditApi();
  }

  callRedditApi(after = '') {
    fetch(`https://www.reddit.com/r/nba.json?after=${after}`).then(resp => resp.json()).then(data => {
      let videos = data.data.children.filter(item => {
        let url = item.data.url;
        return url.match(/streamable/) || url.match(/youtube.com\/watch/) || url.match(/youtu.be/)
      })
      this.setState({
        after: data.data.after,
        videoList: this.state.videoList.concat(videos),
      })
      if (!this.state.currentVideo.url) {
        this.setState({
          currentVideo: this.formatCurrentVideo(videos, 0)
        })
      }
      if(this.state.videoList.length < 10) {
        this.callRedditApi(this.state.after);
      }
    })
  }

  getThumbNail(sourceVideo) {
    let sourceUrl = sourceVideo.data.url;
    if(sourceUrl.match(/streamable/)) {
      return `//cdn.streamable.com/image/${sourceUrl.split('/')[3]}.jpg`
    } else if (sourceUrl.match(/youtube.com\/watch/)){
      return `http://img.youtube.com/vi/${sourceUrl.split('=')[1].substring(0,11)}/0.jpg`;
    } else if(sourceUrl.match(/youtu.be/)) {
      return `http://img.youtube.com/vi/${sourceUrl.split('/')[3]}/0.jpg`;
    }
  }

  formatCurrentVideo(source, position) {
    let sourceVid = source[position];
    let currentVid = {};
    let sourceUrl = sourceVid.data.url;
    let url = '';
    if(sourceUrl.match(/streamable/)) {
      url = `//cdn.streamable.com/video/mp4/${sourceUrl.split('/')[3]}.mp4`
      currentVid.isStreamable = true;
    } else if (sourceUrl.match(/youtube.com\/watch/)){
      url = sourceUrl.split('=')[1].substring(0,11);
      currentVid.isStreamable = false;
    } else if(sourceUrl.match(/youtu.be/)) {
      url = sourceUrl.split('/')[3];
      currentVid.isStreamable = false;
    }
    currentVid.url = url;
    currentVid.position = position + 1;
    currentVid.title = sourceVid.data.title;
    return currentVid;
  }

  setCurrentVideo(index) {
    this.setState({currentVideo: this.formatCurrentVideo(this.state.videoList, index)});
  }
  
  render() {
    const opts = {
      height: 390,
      width: 640,
      playerVars: { // https://developers.google.com/youtube/player_parameters
        autoplay: 1
      }
    }
    const style = {
      listContainer: {
        overflow: 'scroll',
        height: 300,
        width: '100%'
      },
      ulList: {
        overflow: 'scroll',
        width: this.state.videoList.length * 400
      },
      listItem: {
        width: 350,
        display: 'inline',
        float: 'left',
        paddingLeft: 10,
        paddingRight: 10,
        cursor: 'pointer'
      },
      image: {
        height: 200
      }
    } 
    if (!this.state.currentVideo.url) {
      return <div> Loading... </div>
    }
    return (
      <div className="App">
        <div className="App-header">
          <h2>{this.state.currentVideo.title}</h2>
          {this.state.currentVideo.isStreamable ?
          <video style={opts} src={this.state.currentVideo.url} controls autoPlay>
            Your browser does not support the <code>video</code> element.
          </video>
          :
          <YouTube
            videoId={this.state.currentVideo.url}
            opts={opts}
            onEnd={this.setNextVideo.bind(this)}
          />
          }
        </div>
        <div style={style.listContainer}>
          <ul style={style.ulList}>
          {this.state.videoList.map((video, index) => {
            return <li key={index} style={style.listItem} onClick={this.setCurrentVideo.bind(this, index)}>
            <img src={this.getThumbNail(video)} alt={video.data.title} style={style.image} /><br />
            {video.data.title}</li>
          })}
          </ul>
        </div>
      </div>
    );
  }
}

export default App;



